
<template >
    <section>
        <div class="col-md-12 p-0 mt-2">
            <div class="theme-primary partition-full">
                <span class="p-name text-white">{{ $t('Timesheet') }}</span>
                <span class="p-actions float-right">
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white "><em
                            class="fa fa-list"></em><br /><span>{{ $t('Export') }}</span></a>
                    <a id="AddManually" href="javascript:;" class="p-action-btn text-white "><em
                            class="fa fa-calendar"></em><br /><span>{{ $t('thisWeek') }}</span></a>
                    <a v-if="listCheckbox.length > 0" class="p-action-btn text-white" id="DeleteMultiple" title=""
                        href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Delete"
                        @click="deleteMultipleRow">
                        <i class="fa fa-trash"></i><br><span>Delete</span>
                    </a>


                    <!-- <button class="fa fa-trash-o text-danger"></button> -->
                </span>
            </div>
        </div>



        <div class="listing py-3">
            <loader :is-visible="isLoading"></loader>
            <dynamic-form class="daterangecontrolHS" lang="en" v-if="DataLoaded" :buttons="buttons1" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-slotdate="{ data }">
                    <!--<div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                        <div class="form-control py-0 px-2">                            
                            <input id="date_range" v-model="dateselect" value="2023/12/12 - 2023/12/12" name="date_range"
                                type="date" @input="GetStartEndDates"
                                class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue"
                                autocomplete="off" data-toggle="datetimepicker" :title="$t('DateRange')"
                                :placeholder="$t('DateRange')">
                        </div>
                    </div> -->
                    <DatePicker class="d-flex col-12 input-group timesheetdatepicker bg-white p-0" v-model="dateselect" :popover="false" @input="GetStartEndDates">
                                <template #default="{ togglePopover, inputValue, inputEvents }">
                                    <a id="aDateLable" class="pr-2 text-dark bg-white py-1 float-left"
                                style="cursor:default">{{ $t('DateRange') }}: {{ DisplayWeekDate }}</a>
                                    <input id="date_range" class="form-control datepicker_HS" :value="inputValue" v-on="inputEvents" />
                                    <div class="input-group-append cursor-pointer" v-on:click="togglePopover">
                                        <span class="input-group-text">
                                            <em class="fa fa-calendar"></em>
                                        </span>
                                    </div>
                                   
                                </template>
                            </DatePicker>
                </template>
                <template v-slot:tgslot-slotbutton="{ data }">
                    <button class="btn btn-success" @click="FetchData" :title="$t('ShowTimeSheet')">{{ $t('ShowTimeSheet')
                    }} </button>
                </template>
                <template v-slot:tgslot-slotstartenddate="{ data }">
                    <p style="top:-74px;margin-left: 104px;">{{ DisplayWeekDate }}</p>
                </template>
            </dynamic-form>
            <!-- <dynamic-form lang="en" v-if="DataLoaded" :schema="ListSchema" v-on:OnSubmit="onSubmit">
                <template v-slot:tgslot-TimeSheetTable="{ data }">
                    <a id="btnAddNewEntry" class="btn btn-success formbtn mr-2 text-dark" v-on:click="showAddTaskModal"
                        name="btnAddNewEntry" title='Add New Entry' style="float: right;"><i class="fa fa-plus pr-2"></i>Add
                        New Entry</a>
                    -->
            <div class="listing">
                <div class="col-10 mb-2 float-left p-0">
                    <div class="mr-2 d-inline-block">
                        <span class="legends-purple colorboxsquare"></span>{{ $t('AuditableRecords') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_profile colorboxsquare"></span> {{ $t('LeaveHoliday') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="legends_low colorboxsquare"></span>{{ $t('UnPaidLeaveRecords') }}
                    </div>
                    <!-- <div class="mr-2 d-inline-block">
                        <span class="legends_medium colorboxsquare"></span> {{$t('TimeSheet')}}
                    </div> -->
                    <div class="mr-2 d-inline-block">
                        <span class="legends_high colorboxsquare"></span>{{ $t('HolidayLeaveAuditableRecords') }}
                    </div>
                    <div class="mr-2 d-inline-block">
                        <span class="lg-pausedresumed colorboxsquare"></span>{{ $t('PausedResumedTrackEntries') }}
                    </div>
                </div>
                <div class="col-2 mb-2 float-right p-0">
                    <a id="btnAddNewEntry" class="btn btn-primary formbtn" v-on:click="showAddTaskModal"
                        name="btnAddNewEntry" :title="$t('AddNewEntry')" style="float: right;"><em
                            class="fa fa-plus pr-2"></em>{{ $t('AddNewEntry') }}</a>
                </div>
            </div>
            <div class="table-responsive">
                <table class="table table-bordered dt-responsive nowrap mt-0 timesheettable">
                    <thead class="thead-bg">
                        <tr>
                            <th class="align-middle">
                                <div class="form-check form-check-inline">
                                    <div class="custom-control custom-checkbox">
                                        <input type="checkbox" :value="'all'"
                                            :class="{ 'chkItems custom-control-input': true }" name="DeleteInputs"
                                            :id="'allCheck'" @change="allCheckBox" v-model="allCheck"
                                            style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1"
                                            :for="'allCheck'"></label>
                                    </div>

                                </div>
                            </th>
                            <th class="align-middle d-none">{{ $t('ProjectId') }} </th>
                            <th style="min-width: 200px;">{{ $t('ProjectName') }}</th>
                            <th class="align-middle d-none">{{ $t('TaskId') }}</th>
                            <th class="align-middle d-none">{{ $t('TaskName') }}</th>
                            <th v-for="(item, index) in HeaderList" :key="index">
                                {{ getPreviousDay(item) }}
                            </th>
                            <th id="RowTotal">{{ $t('Total') }}</th>
                            <th>{{ $t('Action') }}</th>
                        </tr>
                    </thead>
                    <tbody v-if="computedTimesheetList.length > 0">

                        <tr v-for="(dataItem, dataIndex) in computedTimesheetList" :key="dataIndex">
                            <td class="tabchild" style="max-width:10px;">

                                <div class="form-check form-check-inline">
                                    <div class="custom-control custom-checkbox">
                                        <input type="checkbox" class="list-checkBox"
                                            :class="{ 'chkItems custom-control-input': true }" :value="dataIndex"
                                            name="DeleteInputs" :id="'chk_' + dataIndex" @change="selectedValues"
                                            v-model="listCheckbox" style="display: none;">
                                        <label class="custom-control-label universal-custom-control-label pt-1"
                                            :for="'chk_' + dataIndex"></label>
                                    </div>

                                </div>
                            </td>
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                            <td class="align-middle d-none">{{ dataItem['ProjectId'] }}</td>
                            <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                            <td>
                                <div class="d-inline-block">
                                    <span style="width:90%; float:left;">
                                        <!-- <a title="" value="-1" style="cursor:default;" class="text-dark" href="#"> -->
                                        <span class="d-block font-14 font-weight-medium" style="float: left;">
                                            {{ dataItem['ProjectName'] }}: <span class="text-dark d-block font-12"
                                                style="float: right;">{{ dataItem['TaskDetail'] }}</span>
                                        </span>
                                        <span class="text-dark d-block font-12">{{ dataItem.TaskDescription }}</span>
                                        <span v-if="indexEdit === dataIndex + 1">
                                            <input @change="editChange(dataItem, dataIndex + 1, $event)" type="text"
                                                :value="dataItem['TaskDescription']" /></span>
                                        <!-- </a> -->
                                    </span>
                                    <a class="float-right" @click="Edit(dataIndex + 1)"><em class="fa fa-pencil"></em></a>
                                </div>
                            </td>
                            <td class="align-middle d-none">{{ dataItem['TaskId'] }}</td>
                            <td class="align-middle d-none">{{ dataItem['TaskDetail'] }}</td>
                            <td class="align-middle" v-for="(item, index) in HeaderList" :key="index"
                                :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item }"
                                :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                                <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                                <input class="form-control" type="text" v-model="dataItem[item]"
                                    @input="ChangeCurrentDateTime(dataItem, item)">
                            </td>
                            <td class="align-middle">{{ rowSum(dataItem) }}</td>
                            <td class="single-action text-center"><i class="fa fa-times text-danger"
                                    @click="SingleDeleteData(dataIndex)"></i> </td>
                        </tr>
                    
                        <tr class="background-light" v-for="(dataItem, dataIndex) in computedBreakList" :key="dataIndex">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                            <td class="align-middle "></td>
                            <td class="align-middle d-none">{{ dataItem['ProjectId'] }}</td>
                            <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                            <td class="align-middle font-weight-medium">{{ dataItem['ProjectName'] }}</td>
                            <td class="align-middle d-none">{{ dataItem['TaskId'] }}</td>
                            <td class="align-middle d-none">{{ dataItem['TaskDetail'] }}</td>
                            <td class="align-middle" v-for="(item, index) in HeaderList" :key="index"
                                :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item }"
                                :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                                <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                                <input class="form-control" type="text" v-model="dataItem[item]"
                                    @input="ChangeCurrentDateTime(dataItem, item)">
                            </td>
                            <td class="align-middle">{{ rowSum(dataItem) }}</td>
                            <td class="single-action text-center"><i class="fa fa-times text-danger"
                                    @click="SingleDeleteData(dataIndex)"></i> </td>
                        </tr>
                    </tbody>
                    <tbody v-else>
                        <tr>
                            <td colspan="8" class="text-center">{{ $t('NoRecordfound') }}</td>
                        </tr>
                    
                    </tbody>
                    <tfoot v-if="computedTimesheetList.length > 0">
                        <td class="align-middle d-none"></td>
                        <td class="align-middle "></td>
                        <td>{{ $t('Total') }}:</td>
                        <td class="align-middle d-none"></td>
                        <td class="align-middle d-none"></td>
                        <td v-for="(tData, index) in totalData" :key="index">{{ tData.value }}</td>
                        <td>{{ SumOfFooterRows }}</td>

                    </tfoot>
                </table>
            </div>
            <a id="btnSendForApproval" class="btn btn-success formbtn mr-2 text-light" v-on:click="SaveAsDraft(0, 1)"
                name="" :title="$t('SendForApproval')" style="float: right;"><i class="fa fa-send pr-2"></i>{{
                    $t('SendForApproval') }} </a>
            <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a> -->
            <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark"
                v-on:click="showCopyTimesheet" name="" :title="$t('CopyToOtherUser')" style="float: right;"><i
                    class="fa fa-copy pr-2"></i>{{
                        $t('CopyToOtherUser') }} </a>

            <a id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" v-on:click="CopyLastWeek"
                name="" :title="$t('CopyLastWeek')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                    $t('CopyLastWeek') }} </a>

            <a id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2"
                v-on:click="SaveAsDraft(1, 0)" name="" :title="$t('SaveAsDraft')" style="float: right;"><i
                    class="fa fa-save pr-2"></i>{{ $t('SaveAsDraft') }}</a>
            <!-- <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
        </div>

        <AddTaskTimeSheetModal v-if="addTaskShow" v-bind:store="store" v-on:close="addTaskShow = false"
            v-on:saveTaskEntry="saveTaskEntry"></AddTaskTimeSheetModal>
        <copyTimesheet v-if="openModal" v-on:close="openModal = false"></copyTimesheet>

    </section>
</template>
<script>
import DataService from '../../services/DataService';
import { getPreviousDay } from "@/assets/common/vue-common.js"
import AddTaskTimeSheetModal from "../TimeTrack/modals/AddTaskTimesheetModal.vue"
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import copyTimesheet from "../../components/Modal/Timesheet/CopyTimesheet.vue"
import {
    userTimerInfo
} from "../../stores/TimeTrack"
export default
    {
        components: {
            AddTaskTimeSheetModal,
            DatePicker,
            copyTimesheet
        },
        setup() {
            const store = userTimerInfo()

            return {
                store
            }
        },
        data() {
            return {
                getPreviousDay,
                dateselect: '',
                //formData: [],
                deletedArray: [],
                totalData: [],
                DataLoaded: 0,
                noRecord: false,
                TotalRecords: 0,
                isLoading: false,
                TimesheetList: [],
                BreakList: [],
                LeaveList: [],
                HeaderList: [],
                UserId: null,
                StartDate: null,
                EndDate: null,
                DisplayWeekDate: "This Week",
                StartWeekDay: null,
                totalSeconds: 0,
                //Headers: [],
                addTaskShow: false,
                openModal: false,
                SumOfRows: [],
                SumOfFooterRows: 0,
                allCheck: '',
                listCheckbox: [],
                FormSchema: [
                    {
                        layoutType: "triple",
                        Data: [
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotdate",
                                value: "",
                                placeholder: "Date Range",
                            },
                            {
                                astype: "SelectField",
                                label: "User",
                                name: "user_id",
                                value: "",
                                placeholder: "",
                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },
                            {
                                astype: "SlotField",
                                label: "",
                                name: "slotbutton",
                                value: "",
                                placeholder: "",

                            },
                        ]
                    }
                ]
                , IsValidDate: []
                , isedit: false,
                indexEdit: '',
                carryItems: [],
                oldTaskDesc: [],
                isOldTaskDesc: [],
                privilegeParams: null
            }
        },
        created: async function () {
            var VueObj = this;
            this.privilegeParams = `controller=Timesheet&action=`;
            this.DataLoaded = 1;
            await VueObj.GetStartEndDatesNew();
            await VueObj.GetUsersForTimesheet();
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            await VueObj.FetchData();

        },
        mounted: async function () {
            var VueObj = this;
            //VueObj.SaveAsDraft();
            console.log('mounted')
        },
        computed: {
            computedTimesheetList() {
                return this.TimesheetList
            },
            computedBreakList() {
                return this.BreakList
            },
            computedLeaveList() {
                return this.LeaveList
            },
            filteredDeletedArray() {
                return this.deletedArray;
            },
            rowSums() {
                return this.TimesheetList.map(dataItem => this.rowSum(dataItem));
            },
            footerSums() {
                return this.totalData.map(dataItem => this.footerSums(dataItem));
            }
        },
        methods: {
            allCheckBox() {
                //this.allCheck = "";
                this.listCheckbox = []
                if (this.allCheck) {
                    var inputElements = document.getElementsByClassName('list-checkBox');
                    for (var i = 0; inputElements[i]; ++i) {
                        this.listCheckbox.push(i)
                    }

                } else {
                    this.listCheckbox = []
                }
            },
            selectedValues(e) {
                if (!e.target.checked) {
                    this.allCheck = false
                } else {
                    var inputElements = document.getElementsByClassName('list-checkBox');
                    let check = true
                    for (var i = 0; inputElements[i]; ++i) {
                        if (!inputElements[i].checked) {
                            check = false
                        }
                    }
                    if (check) {
                        this.allCheck = true
                    }
                }

            },
            async deleteMultipleRow() {
                let listDataArray = []
                this.listCheckbox.map((item) => {
                    if (this.computedTimesheetList[item].IsAddNewEntry) {

                        this.computedTimesheetList.splice(item, 1)
                    } else {
                        listDataArray.push(this.computedTimesheetList[item])

                    }
                })

                listDataArray.forEach((item, index) => {
                    delete item['Type']
                })
                if (listDataArray.length > 0) {
                    let obj = {
                        deletedData: listDataArray,
                        userId: this.FormSchema[0].Data[1].value,
                    }
                    var formData = new FormData();
                    formData.append("jsonData", JSON.stringify(obj))
                    const { data } = await DataService.DeleteTimeSheetRecord(obj)
                    console.log("IsAddNewEntry", data)
                }
            },
            footerSums(columnName) {
                const excludedColumns = ["ProjectId", "ProjectName", "TaskDetail", "TaskId"];
                let totalSeconds = 0;

                if (!excludedColumns.includes(columnName)) {
                    this.dataArray.forEach((dataItem) => {
                        const value = dataItem[columnName];
                        if (/^([01]\d|2[0-3]):([0-5]\d)$/.test(value)) {
                            const [hours, minutes] = this.getHoursAndMinutes(value);
                            totalSeconds += hours * 3600 + minutes * 60;
                        }
                    });
                }

                const totalHours = Math.floor(totalSeconds / 3600);
                const totalMinutes = Math.floor((totalSeconds % 3600) / 60);

                return String(totalHours).padStart(2, "0") + ":" + String(totalMinutes).padStart(2, "0");
            },
            columnSums() {
                const excludedColumns = ["ProjectId", "ProjectName", "TaskDetail", "TaskId"];
                const columnSums = {};

                this.HeaderList.forEach((header) => {
                    if (!excludedColumns.includes(header)) {
                        const columnValues = this.TimesheetList.map((dataItem) => {
                            const value = dataItem[header];
                            return /^([01]\d|2[0-3]):([0-5]\d)$/.test(value) ? this.getHoursAndMinutes(value) : 0;
                        });

                        const totalSeconds = columnValues.reduce((sum, [hours, minutes]) => sum + hours * 3600 + minutes * 60, 0);
                        const totalHours = Math.floor(totalSeconds / 3600);
                        const totalMinutes = Math.floor((totalSeconds % 3600) / 60);

                        columnSums[header] = String(totalHours).padStart(2, "0") + ":" + String(totalMinutes).padStart(2, "0");
                    }
                });

                return columnSums;
            },
            rowSum(dataItem) {
                var rowcount = 0;
                return Object.values(dataItem).filter(dataItem => dataItem !== "ProjectId" && dataItem !== "ProjectName" && dataItem !== "TaskDetail" && dataItem != "TaskId" && dataItem !== "RowTotal")
                    .reduce((sum, value) => {
                        const isDatetimeColumn = /^([01]\d|2[0-3]):([0-5]\d)$/.test(value);
                        if (isDatetimeColumn) {
                            let [hours, minutes] = value != null ? value.split(":") : "00:00".split(":");
                            rowcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                        }
                        let hours = Math.floor(rowcount / 3600);
                        let minutes = Math.floor((rowcount % 3600) / 60);
                        sum = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                        return sum;
                    }, 0);
            },
            saveTaskEntry(e) {
                //debugger
                var vm = this;
                var obj = {}
                vm.HeaderList.map((column) => {
                    obj[column] = '00:00'
                })
                let isexist = vm.TimesheetList.find((item) => item.ProjectId == e.ProjectId && item.TaskId == e.TaskId)

                if (!isexist) {
                    Object.assign(obj, e)
                    vm.TimesheetList.push(obj)
                    this.addTaskShow = false
                }
                else {
                    alert('Already Exist');
                    this.addTaskShow = false
                }
            },
            showAddTaskModal() {
                this.addTaskShow = true
            },
            showCopyTimesheet() {
                DataService.CheckPrivilege(this.privilegeParams + 'CopyToOtherUsersTimesheet').then(response => {
                    if (!response.data) {
                        this.ShowAlert(this.$t('UnAuthorized'), "warning", true, "Alert");
                        return false;
                    } else {
                        this.openModal = true;
                    }
                })
                // this.openModal = true;
            },
            ChangeCurrentDateTime(dataItem, item) {
                //debugger
                // this.SumRowColTotalList();
                // this.SumOfRows();
                // this.SumOfFooterRows();
                const timeRegex = /^(0[0-9]|1[0-9]|2[0-3]):[0-5][0-9]$/;

                // Check if the input matches the desired format
                let isvaklid = timeRegex.test(dataItem[item]);
                // if(isvaklid)
                // {
                //     dataItem[item]='';
                // }
                if (dataItem[item].includes(":") && dataItem[item].split(":")[1] != "" && dataItem[item].split(":")[1] != null) {
                    this.SumRowColTotalList();
                    this.SumOfRows();
                    this.SumOfFooterRows();
                }
            },
            async GetUsersForTimesheet() {
                var VueObj = this;
                VueObj.isLoading = true;
                var url = `companyId=${VueObj.GetUserInfo.CompanyId}&loggedUserId=${VueObj.GetUserInfo.ID}&userType=CA&userId=${VueObj.FormSchema[0].Data[1].value}&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            VueObj.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            VueObj.FormSchema[0].Data[1].config.options.push(VueObj.obj);
                        });

                        VueObj.isLoading = false;
                    });
            },
            OnChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            async onSubmit(formfields, e) {
                var vm = this;
                vm.FetchData();
            },
            GetStartEndDates: async function (event, id, a) {
                //console.log("date", this.dateselect)
                var VueObj = this;
                let url = `DateSpan=thisweek&report=`;
                console.log('GetStartEndDates', event, id, a)
                await DataService.GetStartEndDate(url).then(response => {
                    VueObj.StartDate = response.data.dateStart;
                    VueObj.EndDate = response.data.dateEnd;
                    VueObj.DisplayWeekDate = "";
                    VueObj.DisplayWeekDate = response.data.displayText;
                    VueObj.StartWeekDay = response.data.startWeekDay;
                });
                //VueObj.FormSchema[0].Data[0].placeholder = "Date Range: " + VueObj.DisplayWeekDate;
                var offset = 1;
                switch (VueObj.StartWeekDay.toLowerCase()) {
                    case 'tuesday':
                        offset = 2;
                        break;
                    case 'wednesday':
                        offset = 3;
                        break;
                    case 'thursday':
                        offset = 4;
                        break;
                    case 'friday':
                        offset = 5;
                        break;
                    case 'satureday':
                        offset = 6;
                        break;
                    case 'sunday':
                        offset = 0;
                        break;
                    default:
                        offset = 1;
                        break;
                }

                var today = new Date(this.dateselect);
                if (today == '') {
                    today = new Date();
                }
                var currentDay = today.getDay() - offset;
                var startdate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay);
                var enddate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay + 6);
                var dateTime = VueObj.$options.filters.formatDate(startdate) + '-' + VueObj.$options.filters.formatDate(enddate);

                VueObj.StartDate = VueObj.$options.filters.formatDate(startdate);
                VueObj.EndDate = VueObj.$options.filters.formatDate(enddate);
                VueObj.DisplayWeekDate = "";
                VueObj.DisplayWeekDate = dateTime;

