                vm.TimesheetList.forEach((item) => {
                    //debugger
                    var iserowxist = vm.TimesheetList.indexOf(item);

                    if (iserowxist == index) { // && vm.TimesheetList.splice(index).length > 0
                        //console.log('delete', item);
                        if (!item.IsAddNewEntry) {
