                        }
                        vm.TimesheetList.splice(index, 1);
                        this.SumRowColTotalList();
                        this.SumOfRows();
                        this.SumOfFooterRows();
                    }

                });
            },
            SaveAsDraft: async function (isDraft, isSendForApproval) {
                debugger
                var VueObj = this;


                this.oldTaskDesc.forEach((descData) => {
                    console.log(descData);
                });

                VueObj.TimesheetList.forEach((item, index) => {
                    console.log(item);
                    this.oldTaskDesc.forEach((descData) => {
                        if (descData.itemIndex == index + 1) {
                            // console.log('descData',descData);
                            // console.log('item',item);

                            this.$set(item, 'OldTaskDescription', descData.discription);
                        }
                    });
                    delete item['Type']
                    delete item['IsValid']

                    return item
                });
                VueObj.deletedArray.forEach((item,index) =>{
                    delete item['Type']
                    delete item['IsValid']
                    return item
                });

                let obj = {
                    TimesheetList: VueObj.TimesheetList,
                    deletedData: VueObj.deletedArray,
                    breakData: VueObj.BreakList,
                    userId: VueObj.FormSchema[0].Data[1].value,
                    dateFrom: VueObj.StartDate,
                    dateTo: VueObj.EndDate,
                    isDraft: isDraft,
                    isSendForApproval: isSendForApproval
                }
                //var formData = new FormData();
                //formData.append("jsonData", JSON.stringify(obj));

                await DataService.SaveTimeSheetEntry(obj).then(response => {
                    if (response.data != null && response.data != "" && response.data.length > 1) {
                        VueObj.TimesheetList = response.data;

                        VueObj.TimesheetList.forEach((item) => {
                            item.IsValid = JSON.parse(item.IsValid);
                        });
                        console.log('Is Valid date item', VueObj.TimesheetList)

                        VueObj.SumRowColTotalList()
                    }
                    else if (response.data.length == 1) {
                        if (response.data[0].Status_Code == 0) {
                            this.ShowAlert(this.$t("TimeSheetSaved"), 'success', true, this.$t('Alert'));
                            if (isSendForApproval == true) {
                                var fDate = this.StartDate;
                                var tDate = this.EndDate;
                                var tDate = this.EndDate;
                                let url = `dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${this.FormSchema[0].Data[1].value}`
                                DataService.SendForApproval(url).then(response => {
                                    console.log("SendForApproval ", response)
                                    
                                    this.FetchData();
                                });
                            }
                        }
                        else if (response.data[0].Status_Code == -1) {
                            this.ShowAlert(this.$t("Something_Wrong"), 'failure', true, this.$t('Alert'));
                        }
                    }
                });
            },
